import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Formik } from 'formik';
import jQuery from 'jquery';
// import moment from 'moment';
import * as Yup from 'yup';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import {
  getAllSettingSaga,
  editAllSettingsSaga,
  sendNotificationSaga,
  editDefaultFoodDishImageSaga,
} from '../../../store/actions';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';
import {
  SELECT_ONLY_IMAGE,
  // APP_EMAIL_REQUIRED,
  MINIMUM_CART_NUMBER,
  MINIMUM_CART_REQUIRED,
  // CUSTOMER_CARE_NUMBER_CART_NUMBER,
  // CUSTOMER_CARE_NUMBER_CART_REQUIRED,
  GST_REQUIRED,
  // PRE_ORDER_DELIVERY_DAYS_NUMBER,
  // PRE_ORDER_DELIVERY_DAYS_REQUIRED,
  // COMMISSION_NUMBER,
  // COMMISSION_REQUIRED,
  PACKAGING_CHARGES_NUMBER,
  PACKAGING_CHARGES_REQUIRED,
  DELIVERY_CHARGE_KM_NUMBER,
  DELIVERY_CHARGE_KM_REQUIRED,
  ORDER_ACCEPT_TIME_NUMBER,
  ORDER_ACCEPT_TIME_REQUIRED,
  ORDER_PREPARING_TIME_NUMBER,
  ORDER_PREPARING_TIME_REQUIRED,
  TITLE_REQUIRED,
  DEVICE_TYPE_REQUIRED,
  MESSAGE_REQUIRED,
  CHEF_PLATFORM_FEE_REQUIRED,
  TCS_REQUIRED,
  PAYMENT_GATEWAY_TRANSACTION_PERCENTAGE_REQUIRED,
  CHEF_PLATFORM_FEE_NUMBER,
  TCS_NUMBER,
  PAYMENT_GATEWAY_TRANSACTION_PERCENTAGE_NUMBER,
} from '../../../constants/errorConstants';

// eslint-disable-next-line no-unused-vars
const Settings = props => {
  // eslint-disable-next-line no-unused-vars
  const [mounted, setMounted] = useState(false);
  const [isDisable, setIsDisable] = useState(false);
  const [imageError, setImageError] = useState('');
  const [photoURL, setPhotoURL] = useState('');
  // const baseURL = process.env.REACT_APP_END_POINT_IMG_URL;
  const {
    setting: {
      GST,
      // appEmail,
      // customerCareNumber,
      // commissionPercentage,
      defaultChefImage,
      defaultChefImageThumb,
      minimumCartValue,
      // minimumCartValueUpdatedAt,
      orderAcceptTime,
      // preOrderDeliveryDays,
      packagingCharges,
      deliveryChargeBasedKms,
      deliveryChargeBasedPrice,
      orderPreparingTime,
      chefPlatformFee,
      TCS,
      paymentGatewayTransactionPercentage,
    },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.setting);
  const { open } = useSelector(state => state.modal);
  const sendNotification = {
    title: '',
    deviceType: 'all',
    message: '',
  };

  const dispatch = useDispatch();

  useEffect(() => {
    setMounted(true);
    dispatch(getAllSettingSaga());
    jQuery('[data-toggle="tooltip"]').tooltip();
    return () => setMounted(false);
  }, []);

  const handleImageChange = e => {
    // e.target.files
    if (
      e.target.files.length !== 0 &&
      e.target.files[0].type.includes('image')
    ) {
      const file = e.target.files[0];
      setImageError('');
      setPhotoURL(URL.createObjectURL(file));
      dispatch(editDefaultFoodDishImageSaga({ photo: file }));
    } else if (e.target.files[0].type.includes('image')) {
      setImageError(SELECT_ONLY_IMAGE);
    }
  };

  const validationSchema = Yup.object({
    // appEmail: Yup.string().email().required(APP_EMAIL_REQUIRED),
    minimumCartValue: Yup.number()
      .typeError(MINIMUM_CART_NUMBER)
      .required(MINIMUM_CART_REQUIRED),
    // customerCareNumber: Yup.number()
    //   .typeError(CUSTOMER_CARE_NUMBER_CART_NUMBER)
    //   .required(CUSTOMER_CARE_NUMBER_CART_REQUIRED),
    GST: Yup.string().trim().required(GST_REQUIRED),
    // preOrderDeliveryDays: Yup.number()
    //   .typeError(PRE_ORDER_DELIVERY_DAYS_NUMBER)
    //   .required(PRE_ORDER_DELIVERY_DAYS_REQUIRED),
    // commissionPercentage: Yup.number()
    //   .typeError(COMMISSION_NUMBER)
    //   .required(COMMISSION_REQUIRED),
    packagingCharges: Yup.number()
      .typeError(PACKAGING_CHARGES_NUMBER)
      .required(PACKAGING_CHARGES_REQUIRED),
    deliveryChargeBasedKms: Yup.number()
      .typeError('Delivery charge km must be a number')
      .required('Delivery charge km is required'),
    deliveryChargeBasedPrice: Yup.number()
      .typeError(DELIVERY_CHARGE_KM_NUMBER)
      .required(DELIVERY_CHARGE_KM_REQUIRED),
    orderAcceptTime: Yup.number()
      .typeError(ORDER_ACCEPT_TIME_NUMBER)
      .required(ORDER_ACCEPT_TIME_REQUIRED),
    orderPreparingTime: Yup.number()
      .typeError(ORDER_PREPARING_TIME_NUMBER)
      .required(ORDER_PREPARING_TIME_REQUIRED),
    chefPlatformFee: Yup.number()
      .typeError(CHEF_PLATFORM_FEE_NUMBER)
      .required(CHEF_PLATFORM_FEE_REQUIRED),
    TCS: Yup.number().typeError(TCS_NUMBER).required(TCS_REQUIRED),
    paymentGatewayTransactionPercentage: Yup.number()
      .typeError(PAYMENT_GATEWAY_TRANSACTION_PERCENTAGE_NUMBER)
      .required(PAYMENT_GATEWAY_TRANSACTION_PERCENTAGE_REQUIRED),
  });

  const validationSchemaNotification = Yup.object({
    title: Yup.string().trim().required(TITLE_REQUIRED),
    deviceType: Yup.string().trim().required(DEVICE_TYPE_REQUIRED),
    message: Yup.string().trim().required(MESSAGE_REQUIRED),
  });

  return (
    <div className="wrapper wrapper-content">
      <div className="row" id="setting">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Settings</h2>
              </div>
              <div className="tabs-container" id="tab-new">
                <ul className="nav nav-tabs">
                  <li className="active">
                    <a data-toggle="tab" href="#tab-1">
                      Configuration Settings
                    </a>
                  </li>
                  <li className="">
                    <a data-toggle="tab" href="#tab-2">
                      Vendor Images
                    </a>
                  </li>
                  <li className="">
                    <a data-toggle="tab" href="#tab-3">
                      Push Notification
                    </a>
                  </li>
                </ul>
                <div className="tab-content">
                  <div id="tab-1" className="tab-pane active">
                    <div className="panel-body" id="setting-col">
                      <div className="row">
                        <div className="col-lg-12">
                          <Formik
                            enableReinitialize
                            initialValues={{
                              // appEmail,
                              GST,
                              // customerCareNumber,
                              // commissionPercentage,
                              defaultChefImage,
                              minimumCartValue,
                              orderAcceptTime,
                              // preOrderDeliveryDays,
                              packagingCharges,
                              deliveryChargeBasedKms,
                              deliveryChargeBasedPrice,
                              orderPreparingTime,
                              chefPlatformFee,
                              TCS,
                              paymentGatewayTransactionPercentage,
                            }}
                            validationSchema={validationSchema}
                            onSubmit={(values, { setSubmitting }) => {
                              setIsDisable(true);
                              dispatch(
                                editAllSettingsSaga({ values, setIsDisable }),
                              );
                              setSubmitting(false);
                            }}
                          >
                            {({
                              values,
                              errors,
                              touched,
                              handleChange,
                              handleBlur,
                              handleSubmit,
                              resetForm,
                            }) => (
                              <div
                                className="ibox"
                                style={{ boxShadow: 'none' }}
                              >
                                <form onSubmit={handleSubmit}>
                                  <div className="ibox-content">
                                    {/* <div className="row">
                                      <div className="col-xl-3 col-lg-4 col-md-4">
                                        <label htmlFor="coupon-coupon_value">
                                          App Email:
                                        </label>
                                      </div>
                                      <div className="col-xl-9 col-lg-8 col-md-8">
                                        <input
                                          type="text"
                                          id="coupon-coupon_value"
                                          className="form-control"
                                          name="appEmail"
                                          value={values.appEmail}
                                          onChange={handleChange}
                                          onBlur={handleBlur}
                                          aria-required="true"
                                        />
                                        <div className="error-message left-align">
                                          {errors.appEmail &&
                                            touched.appEmail &&
                                            errors.appEmail}
                                        </div>
                                      </div>
                                    </div> */}
                                    {/* <div className="row">
                                      <div className="col-lg-4 col-md-4">
                                        <label htmlFor="coupon-coupon_value">
                                          Customer Care Number:
                                        </label>
                                      </div>
                                      <div className="col-lg-8 col-md-8">
                                        <input
                                          type="text"
                                          id="coupon-coupon_value"
                                          className="form-control"
                                          name="customerCareNumber"
                                          value={values.customerCareNumber}
                                          onChange={handleChange}
                                          onBlur={handleBlur}
                                          aria-required="true"
                                        />
                                        <div className="error-message left-align">
                                          {errors.customerCareNumber &&
                                            touched.customerCareNumber &&
                                            errors.customerCareNumber}
                                        </div>
                                      </div>
                                    </div> */}
                                    <div className="row">
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              Minimum Cart Amount(₹):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            {/* <div className="row">
                                              <div className="col-lg-6 col-md-6 col-sm-6"> */}
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="minimumCartValue"
                                              value={values.minimumCartValue}
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.minimumCartValue &&
                                                touched.minimumCartValue &&
                                                errors.minimumCartValue}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              GST Tax(%):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="GST"
                                              value={values.GST}
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.GST &&
                                                touched.GST &&
                                                errors.GST}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                    {/* <div className="row">
                                      <div className="col-lg-4 col-md-4">
                                        <label htmlFor="coupon-coupon_value">
                                          Pre Order Delivery Days:
                                        </label>
                                      </div>
                                      <div className="col-lg-8 col-md-8">
                                        <input
                                          type="text"
                                          id="coupon-coupon_value"
                                          className="form-control"
                                          name="preOrderDeliveryDays"
                                          value={values.preOrderDeliveryDays}
                                          onChange={handleChange}
                                          onBlur={handleBlur}
                                          aria-required="true"
                                        />
                                        <div className="error-message left-align">
                                          {errors.preOrderDeliveryDays &&
                                            touched.preOrderDeliveryDays &&
                                            errors.preOrderDeliveryDays}
                                        </div>
                                      </div>
                                    </div> */}
                                    <div className="row">
                                      {/* <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label htmlFor="coupon-coupon_value">
                                              Commission(%):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="commissionPercentage"
                                              value={
                                                values.commissionPercentage
                                              }
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.commissionPercentage &&
                                                touched.commissionPercentage &&
                                                errors.commissionPercentage}
                                            </div>
                                          </div>
                                        </div>
                                      </div> */}
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              Packaging Charges(₹):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="packagingCharges"
                                              value={values.packagingCharges}
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.packagingCharges &&
                                                touched.packagingCharges &&
                                                errors.packagingCharges}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              Razorpay Transaction charges(%):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="paymentGatewayTransactionPercentage"
                                              value={
                                                values.paymentGatewayTransactionPercentage
                                              }
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.paymentGatewayTransactionPercentage &&
                                                touched.paymentGatewayTransactionPercentage &&
                                                errors.paymentGatewayTransactionPercentage}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                    <div className="row">
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              Delivery Charge Per Additional KM:
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="deliveryChargeBasedKms"
                                              value={
                                                values.deliveryChargeBasedKms
                                              }
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.deliveryChargeBasedKms &&
                                                touched.deliveryChargeBasedKms &&
                                                errors.deliveryChargeBasedKms}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              Delivery Charge Base Price(₹):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="deliveryChargeBasedPrice"
                                              value={
                                                values.deliveryChargeBasedPrice
                                              }
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.deliveryChargeBasedPrice &&
                                                touched.deliveryChargeBasedPrice &&
                                                errors.deliveryChargeBasedPrice}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                    <div className="row">
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              Time to accept order(Minutes):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="orderAcceptTime"
                                              value={values.orderAcceptTime}
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.orderAcceptTime &&
                                                touched.orderAcceptTime &&
                                                errors.orderAcceptTime}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              Time to prepare order(Minutes):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="orderPreparingTime"
                                              value={values.orderPreparingTime}
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.orderPreparingTime &&
                                                touched.orderPreparingTime &&
                                                errors.orderPreparingTime}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                    <div className="row">
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              Monthly Vendor Platform Fee(₹):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="chefPlatformFee"
                                              value={values.chefPlatformFee}
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.chefPlatformFee &&
                                                touched.chefPlatformFee &&
                                                errors.chefPlatformFee}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                      <div className="col-lg-6 col-md-12">
                                        <div className="row">
                                          <div className="col-lg-6 col-md-6">
                                            <label
                                              htmlFor="coupon-coupon_value"
                                              className="required-field"
                                            >
                                              TCS(%):
                                            </label>
                                          </div>
                                          <div className="col-lg-6 col-md-6">
                                            <input
                                              type="text"
                                              id="coupon-coupon_value"
                                              className="form-control"
                                              name="TCS"
                                              value={values.TCS}
                                              onChange={handleChange}
                                              onBlur={handleBlur}
                                              aria-required="true"
                                            />
                                            <div className="error-message left-align">
                                              {errors.TCS &&
                                                touched.TCS &&
                                                errors.TCS}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                                  <div className="error-message">
                                    {errorMsg}
                                  </div>
                                  <div className="text-center">
                                    <button
                                      type="submit"
                                      className="btn btn-primary m-l-sm"
                                      disabled={isDisable}
                                    >
                                      Update
                                    </button>
                                    <button
                                      type="button"
                                      disabled={isDisable}
                                      className="btn btn-primary m-l-sm"
                                      onClick={() => resetForm()}
                                    >
                                      Reset
                                    </button>
                                  </div>
                                </form>
                              </div>
                            )}
                          </Formik>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div id="tab-2" className="tab-pane">
                    <div className="panel-body">
                      <div className="row">
                        <div className="col-md-6 col-sm-6">
                          <form runat="server">
                            <h4>
                              Default image for Food Dish
                              <i
                                className="fa fa-info-circle"
                                style={{ marginLeft: 5, color: '#404E67' }}
                                aria-hidden="true"
                                data-toggle="tooltip"
                                data-placement="top"
                                title="The format of the image should be jpg or png"
                              />
                            </h4>
                            <input
                              type="file"
                              id="imgInp"
                              onChange={handleImageChange}
                            />
                            <div className="error-message left-align">
                              {imageError}
                            </div>
                            {defaultChefImageThumb || photoURL !== '' ? (
                              // <img
                              //   id="blah"
                              //   // src="img/images-banner.png"
                              //   alt="Vendor"
                              //   style={{ width: '150px', marginTop: '150px' }}
                              // />
                              <div className="table-image-container">
                                <img
                                  src={
                                    photoURL === ''
                                      ? defaultChefImageThumb
                                      : photoURL
                                  }
                                  alt="Default dish"
                                />
                              </div>
                            ) : (
                              <span>No image available</span>
                            )}
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div id="tab-3" className="tab-pane">
                    <div className="panel-body">
                      <div className="row">
                        <Formik
                          enableReinitialize
                          initialValues={sendNotification}
                          validationSchema={validationSchemaNotification}
                          onSubmit={(values, { setSubmitting, resetForm }) => {
                            setIsDisable(true);
                            dispatch(
                              sendNotificationSaga({ values, setIsDisable }),
                            );
                            setSubmitting(false);
                            resetForm({ values: '' });
                          }}
                        >
                          {({
                            values,
                            errors,
                            touched,
                            handleChange,
                            handleBlur,
                            handleSubmit,
                            resetForm,
                          }) => (
                            <form onSubmit={handleSubmit}>
                              <div className="col-md-12">
                                <div className="row">
                                  <div className="col-xl-3 col-lg-4 col-md-4">
                                    <label
                                      htmlFor="coupon-coupon_value"
                                      className="required-field"
                                    >
                                      Title:
                                    </label>
                                  </div>
                                  <div className="col-xl-9 col-lg-8 col-md-8">
                                    <input
                                      type="text"
                                      id="coupon-coupon_value"
                                      className="form-control"
                                      name="title"
                                      value={values.title}
                                      onChange={handleChange}
                                      onBlur={handleBlur}
                                      aria-required="true"
                                    />
                                    <div className="error-message left-align">
                                      {errors.title &&
                                        touched.title &&
                                        errors.title}
                                    </div>
                                  </div>
                                </div>
                                <div className="row">
                                  <div className="col-xl-3 col-lg-4 col-md-4">
                                    <label htmlFor="coupon-coupon_value">
                                      App Type:
                                    </label>
                                  </div>
                                  <div className="col-xl-9 col-lg-8 col-md-8">
                                    <select
                                      className="form-control"
                                      name="deviceType"
                                      value={values.deviceType}
                                      onChange={handleChange}
                                      onBlur={handleBlur}
                                    >
                                      <option value="all">All</option>
                                      <option value="web">Web - Vendor</option>
                                      <option value="mobile">
                                        Mobile (All users)
                                      </option>
                                      <option value="ios">IOS</option>
                                      <option value="android">Android</option>
                                    </select>
                                    <div className="error-message left-align">
                                      {errors.deviceType &&
                                        touched.deviceType &&
                                        errors.deviceType}
                                    </div>
                                  </div>
                                </div>
                                <div className="row">
                                  <div className="col-xl-3 col-lg-4 col-md-4">
                                    <label
                                      htmlFor="coupon-coupon_value"
                                      className="required-field"
                                    >
                                      Message:
                                    </label>
                                  </div>
                                  <div className="col-xl-9 col-lg-8 col-md-8">
                                    <textarea
                                      className="form-control"
                                      name="message"
                                      value={values.message}
                                      onChange={handleChange}
                                      onBlur={handleBlur}
                                    />
                                    <div className="error-message left-align">
                                      {errors.message &&
                                        touched.message &&
                                        errors.message}
                                    </div>
                                  </div>
                                </div>
                                <div className="text-center">
                                  <button
                                    type="submit"
                                    className="btn btn-primary  m-l-sm"
                                    disabled={isDisable}
                                  >
                                    Send
                                  </button>
                                  <button
                                    type="button"
                                    disabled={isDisable}
                                    className="btn btn-primary m-l-sm"
                                    data-dismiss="modal"
                                    onClick={() => resetForm()}
                                  >
                                    Reset
                                  </button>
                                </div>
                              </div>
                            </form>
                          )}
                        </Formik>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {open && <AlertMessageModal />}
    </div>
  );
};

export default SetTokenHeader(Settings, axios);
